import { Editor } from "@toast-ui/vue-editor";

export default {
  middleware: "isAuthenticated", // it will use `isAuthenticated` middleware
  components: {
    editor: Editor,
  },
  data() {
    return {
      Word: "",
      synonyms: "",
      origin: "",
      Description: "",
      editorText: "This is initialValue.",
      editorOptions: {
        hideModeSwitch: true,
        language: "an_US",
        toolbarItems: [
          "heading",
          "bold",
          "italic",
          "strike",
          "divider",
          "hr",
          "quote",
          "divider",
          "ul",
          "ol",
          "divider",

          "outdent",
          "divider",

          "link",
          "divider",
        ],
        language: "ar",
      },
      editorHtml: "",
      editorVisible: false,
    };
  },
  methods: {
    async submitWord() {
      try {
        const user = await this.$axios
          .$post("/api/Word", {
            word: this.Word,
            synonyms: this.synonyms,
            origin: this.origin,
            description: this.Description,
            AddBy: this.getUserInfo.fullname,
          })
          .then(() => {
            this.Word = "";
            this.synonyms = "";
            this.origin = "";
            this.Description = "";
          });

        // console.log("word subbmited");
        this.$buefy.toast.open({
          message: "word added successfully!!",
          // message: "word subbmited correctly!",

          type: "is-success",
        });
      } catch (err) {
        console.log(err);
        this.$buefy.toast.open({
          duration: 5000,
          message: `Something's not good, ðŸ˜¢`,
          type: "is-danger",
        });
      }
    },
  },
  computed: {
    getUserInfo() {
      return this.$store.getters.getUserInfo;
    },
    allowSubmit() {
      if ((this.Word && this.synonyms) || (this.Word && this.Description))
        return true;
      else return false;
    },
  },
};
